import React, { Component } from "react";
import "./style.css";

// Depending on the current path, this component sets the "active" class on the appropriate navigation link item
class EmployeeBar extends Component {
  state = {
    sortedField: "",
  };

  switchFields(field) {
    if (!field) {
      return;
    }
    if (field === "name" || field === "phone" || field === "email") {
      this.setState({ sortedField: field });
    }
  }

  sortFields(field) {
    switch (field) {
      case "name":
        const cards = document.querySelectorAll(".card");
        console.log(cards);

        break;

      case "phone":
        break;

      case "email":
        break;

      case "":
        break;

      default:
        break;
    }
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light justify-content-center">
          <ul className="navbar-nav row col-12">
            <li className="nav-item col-3">
              <button to="/">Image</button>
            </li>
            <li className="nav-item col-3">
              <button type="button" onClick={this.switchFields("name")}>
                Name
              </button>
            </li>
            <li className="nav-item col-3">
              <button type="button" onClick={this.switchFields("name")}>
                Phone
              </button>
            </li>
            <li className="nav-item col-3">
              <button type="button" onClick={this.switchFields("name")}>
                Email
              </button>
            </li>
          </ul>
        </nav>
        <div>{this.sortFields(this.sortedField)}</div>
      </div>
    );
  }
}

export default EmployeeBar;
